












































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "../../../api";
import Utils from "@/utils/utils";
import Bus from "./Bus";
import { ElUpload } from "element-ui/types/upload";

@Component({})
export default class FormObject extends Vue {
  @Prop()
  baseTypeId!: string;
  @Prop()
  nodeInfo!: any;

  // 
  editableStatus: boolean = false;
  // ID
  parentId: any = null;
  // 
  instanceForm: any = {
    baseTypeId: "",
    code: "",
    display: "",
    extInfo: [],
    image: "",
    location: "",
    name: "",
    orgId: "",
    status: "",
    typeDetailId: ""
  };
  // 
  instanceFormRules: any = {
    name: { required: true, message: "", trigger: "blur" },
    // code: { required: true, message: "", trigger: "blur" },
    orgId: { required: true, message: "", trigger: "blur" },
    location: { required: true, message: "", trigger: "blur" },
    typeDetailId: { required: true, message: "", trigger: "blur" }
  };
  // 
  orgInfo: any = {
    options: [],
    defaultProps: {
      label: "name",
      value: "id",
      checkStrictly: true,
      emitPath: false
    }
  };
  // 
  baseTypeTree: Array<any> = [];
  defaultProps: any = {
    label: "name",
    value: "id",
    checkStrictly: true,
    emitPath: false
  };
  // 
  dialogVisible: boolean = false;
  // 
  customOptions: Array<any> = [];
  fileLoading: boolean = false;
  // 
  isDisable: boolean = true;
  // 
  isEdit: boolean = true;

  // @Watch("baseTypeId", { immediate: true, deep: true })

  created() {
    this.getOrgTree();
  }
  mounted() {
    Bus.$on("add", (baseTypeId: string, parentNodeInfo: any) => {
      this.isEdit = false;
      this.isDisable = false;
      this.editableStatus = true;
      this.instanceForm = {
        image: "",
        location: ""
      };
      this.parentId = parentNodeInfo.id;
      this.getObjectTypeTree(baseTypeId);
    });
    Bus.$on("view", (data: any) => {
      this.isDisable = true;
      this.editableStatus = false;
      this.getObjectTypeTree();
      this.getFormData(data);
    });
  }
  beforeDestroy() {
    Bus.$off(["add", "edit", "view", "back"]);
  }

  // 
  // @Watch("baseTypeId", { immediate: true, deep: true })
  getObjectTypeTree(id: string = "") {
    if (id) {
      this.$http.get(api.ledger.getObjectTypeTree + "/" + id).then((response: any) => {
        if (response.data.status) {
          this.baseTypeTree = response.data.data;
        } else {
          this.baseTypeTree = [];
        }
      });
    } else {
      this.$http.get(api.ledger.getObjectTypeTree).then((response: any) => {
        if (response.data.status) {
          this.baseTypeTree = response.data.data;
        } else {
          this.baseTypeTree = [];
        }
      });
    }
  }

  // @Watch("nodeInfo", { immediate: true, deep: true })
  getFormData(nodeInfo: any) {
    if (nodeInfo.businessObjectId && nodeInfo.businessType === "GeneralObject") {
      // this.isDisable = true;
      this.getObjectInfo(nodeInfo.businessObjectId).then((response: any) => {
        this.instanceForm = response;
        if (this.instanceForm.extInfoVO) {
          this.customOptions = this.instanceForm.extInfoVO;
        }
      });
    } else {
      // this.isDisable = false;
      // this.instanceForm = {};
    }
  }
  async getObjectInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.ledger.getObjectDetail, {
      params: {
        id: id
      }
    });
    if (status) {
      return data;
    }
  }
  // 
  getOrgTree() {
    this.$http.get(api.instance.getOrgTree).then((response: any) => {
      if (response.data.status) {
        Utils.removeEmptyChildren(response.data.data);
        this.orgInfo.options = response.data.data;
      }
    });
  }
  // 
  customHttpRequest(data: any) {
    this.fileLoading = true;
    let fData = Utils.createUploadForm(data.file);
    this.$http.post(data.action, fData.formData, fData.config).then((response: any) => {
      this.fileLoading = false;
      if (response.data.status) {
        // this.instanceForm.image = response.data.data.accessUrl;
        this.$set(this.instanceForm, "image", response.data.data.accessUrl);
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  handleRemove() {
    this.instanceForm.image = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
      this.instanceForm.image = "";
    });
  }
  // 
  updateObjectLocation(address: any, data: any, lnglat: any) {
    this.instanceForm.location = JSON.stringify({
      address: address,
      position: lnglat
    });
  }

  // 
  getCustomOptions() {
    this.$http.get(api.interfaceCommon.getTemplateByTypeDetaiId + "/" + this.instanceForm.typeDetailId).then((response: any) => {
      if (response.data.status) {
        this.customOptions = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // CustomInput
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["custom-input"];
    if (!customInput) {
      return null;
    }
    return await customInput.formValidation();
  }

  // 
  formEdit() {
    if (this.instanceForm.id) {
      this.editableStatus = true;
      this.isDisable = false;
      this.isEdit = true;
    } else {
      this.$message.warning("");
    }
  }
  // 
  backToInitial() {
    this.editableStatus = false;
    this.isDisable = true;
  }

  // 
  async saveObject(formData: any) {
    let tempVO = Object.assign({}, formData, { parentId: this.parentId }, { baseTypeId: this.baseTypeId });
    let url: any;
    if (formData && formData.id) {
      url = api.ledger.updateObject;
    } else {
      url = api.ledger.addObject;
    }
    let {
      data: { data, status, message }
    } = await this.$http.post(url, tempVO);
    if (status) {
      this.$message.success(message);
      this.editableStatus = false;
      this.isDisable = true;
      Bus.$emit("refresh");
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }

  async sendFormData() {
    let flag = false;
    let data = await this.getFilledCustomOptions();
    if (data) {
      this.instanceForm.extInfo = await this.getFilledCustomOptions();
    } else {
      this.instanceForm.extInfo = [];
    }
    delete this.instanceForm.extInfoVO;
    (this.$refs.instanceForm as any).validate((valid: any) => {
      if (valid) {
        this.saveObject(this.instanceForm);
        // this.$emit("sendFormData", this.instanceForm);
        flag = true;
      }
    });
    return flag;
  }
}
