
































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "../../../api";
import SelectDialog from "@/microApps/businessObjectManage/views/instance/components/SelectDialog.vue";

@Component({
  components: { SelectDialog }
})
export default class BindTable extends Vue {
  @Prop()
  baseTypeId!: string;
  @Prop()
  baseTypeCode!: string;
  @Prop()
  treeNodeInfo!: any;

  // 
  pagination: any = {
    // 
    query: {},
    // 
    tableData: [],
    // 
    current: 1,
    // 
    size: 10,
    // 
    total: 0
  };

  // 
  selectDialogVisible: boolean = false;

  @Watch("baseTypeId", { immediate: true })
  getAllBind(id: any) {
    console.log("");
    id && console.log(id);
    id &&
      this.$http
        .post(api.objectDetail.getAllBind, {
          baseObjectTypeId: id,
          currentId: this.treeNodeInfo.id,
          keyword: "",
          pageIndex: 1,
          pageSize: 10
        })
        .then((response: any) => {
          if (response.data.status) {
            this.pagination.tableData = response.data.data.records;
            this.pagination.total = response.data.data.total;
          }
        });
  }
  // 
  editBindTableInfo() {
    this.selectDialogVisible = true;
    // (this.$refs["selectDialog"] as any).getAllObjectByType();
  }

  // 
  closeSelectDialog() {
    this.getAllBind(this.baseTypeId);
    this.selectDialogVisible = false;
  }

  search() {}

  handleSizeChange() {}

  handleCurrentChange() {}
}
