






























































































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "../../../api";
import Utils from "@/utils/utils";
import Bus from "./Bus";
import { ElUpload } from "element-ui/types/upload";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class FormCar extends Vue {
  @Prop()
  nodeInfo!: any;

  // 
  editableStatus: boolean = false;
  // ID
  parentId: any = null;
  formLoading: boolean = false;
  // ()
  vehicleDetailsForm: any = {};
  // ()
  vehicleDetailsFormRules: any = {
    // code: { required: true, message: "", trigger: "blur" },
    licensePlate: {
      required: true,
      message: "",
      trigger: "blur"
    },
    orgId: { required: true, message: "", trigger: "blur" },
    typeDetailId: {
      required: true,
      message: "",
      trigger: "blur"
    },
    status: { required: true, message: "", trigger: "blur" }
  };
  // 
  vehicleTypeOptions: Array<any> = [];
  // 
  orgOptions: Array<any> = [];
  // 
  staffsOptions: Array<any> = [];
  // 
  customOptions: Array<any> = [];
  // 
  statusOptions: Array<any> = [
    {
      value: true,
      label: ""
    },
    {
      value: false,
      label: ""
    }
  ];
  // 
  fileLoading: boolean = false;
  // 
  isDisable: boolean = true;
  hasCustomRole: boolean = qTouch.getBaseTypeByCode("car");
  // 
  isEdit: boolean = true;

  created() {
    this.getVehicleTypes();
    this.getOrganizations();
    this.getPersonList("");
  }

  mounted() {
    Bus.$on("add", (baseTypeId: string, parentNodeInfo: any) => {
      this.isEdit = false;
      this.isDisable = false;
      this.editableStatus = true;
      this.parentId = parentNodeInfo.id;
      this.vehicleDetailsForm = {};
    });
    Bus.$on("view", (data: any) => {
      this.isDisable = true;
      this.editableStatus = false;
      this.getFormData(data);
    });
  }

  beforeDestroy(): void {
    // Bus.$off([this.busAdd, this.busEdit, this.busView, this.busBack]);
    Bus.$off(["add", "edit", "view", "back"]);
  }

  // @Watch("nodeInfo", { immediate: true, deep: true })
  getFormData(nodeInfo: any) {
    if (nodeInfo.businessObjectId && nodeInfo.businessType === "Car") {
      this.isDisable = true;
      this.getCarInfo(nodeInfo.businessObjectId).then((response: any) => {
        this.vehicleDetailsForm = response;
        if (this.staffsOptions.map((i: any) => i.id).filter((o: any) => o === this.vehicleDetailsForm.personId).length === 0) {
          this.vehicleDetailsForm.personId = "";
        }
        if (this.vehicleDetailsForm.extInfoVO) {
          this.customOptions = this.vehicleDetailsForm.extInfoVO;
        }
      });
    } else {
      this.vehicleDetailsForm = {};
      this.isDisable = false;
    }
  }
  // 
  async getCarInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.baseInfo.getCaInfo + "/" + id);
    if (status) {
      return data;
    }
  }
  // 
  getVehicleTypes() {
    this.$http.get(api.interfaceCommon.findTypeTree + "/car").then((response: any) => {
      if (response.data.status) {
        let data = response.data.data;
        this.vehicleTypeOptions = data ? data : [];
        // this.$nextTick(() => {
        //   let typeDetailId = this.vehicleDetailsForm.typeDetailId;
        //   typeDetailId && (this.$refs.typeTree as ElTree).setCheckedKeys([typeDetailId]);
        // });
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 
  getOrganizations() {
    this.$http.get(api.interfaceCar.getNewOrgTree).then((response: any) => {
      if (response.data.status) {
        this.orgOptions = response.data.data;
        // this.$nextTick(() => {
        //   (this.$refs.orgTree as ElTree).setCheckedKeys([this.vehicleDetailsForm.orgId]);
        // });
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 
  getPersonList(name: string) {
    this.$http
      .post(api.interfaceCar.getPersonPaging, {
        current: 1,
        size: 100,
        total: 0,
        query: {
          name: name,
          organizationId: this.vehicleDetailsForm.orgId
        }
      })
      .then((res: any) => {
        if (res.status) {
          if (res.data.status) {
            this.staffsOptions = res.data.data.records;
            // this.pagination.total = res.data.data.total;
          } else {
            this.$message.error(res.data.message);
          }
        }
      });
  }
  remoteMethod(query: string) {
    this.getPersonList(query);
  }
  // 
  customHttpRequest(data: any) {
    this.fileLoading = true;
    let fData = Utils.createUploadForm(data.file);
    this.$http.post(data.action, fData.formData, fData.config).then((response: any) => {
      this.fileLoading = false;
      if (response.data.status) {
        // this.vehicleDetailsForm.image = response.data.data.accessUrl;
        this.$set(this.vehicleDetailsForm, "image", response.data.data.accessUrl);
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  handleRemove() {
    this.vehicleDetailsForm.image = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }
  // 
  getCustomOptions() {
    this.$http.get(api.interfaceCommon.getTemplateByTypeDetaiId + "/" + this.vehicleDetailsForm.typeDetailId).then((response: any) => {
      if (response.data.status) {
        this.customOptions = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // CustomInput
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["custom-input"];
    if (!customInput) {
      return null;
    }
    return await customInput.formValidation();
  }

  // 
  formEdit() {
    if (this.vehicleDetailsForm.id) {
      this.editableStatus = true;
      this.isDisable = false;
      this.isEdit = true;
    } else {
      this.$message.warning("");
    }
  }
  // 
  backToInitial() {
    this.editableStatus = false;
    this.isDisable = true;
  }

  // 
  async saveCar(formData: any) {
    let url: any;
    if (formData && formData.id) {
      url = api.baseInfo.updateCar;
    } else {
      url = api.baseInfo.addCar;
    }
    let {
      data: { data, status, message }
    } = await this.$http({
      method: "POST",
      url: url,
      data: formData,
      params: {
        parentId: this.parentId,
        treeFlag: true
      }
    });
    if (status) {
      this.$message.success(message);
      this.editableStatus = false;
      this.isDisable = true;
      Bus.$emit("refresh");
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }

  async sendFormData() {
    let data = await this.getFilledCustomOptions();
    if (data) {
      this.vehicleDetailsForm.extInfo = await this.getFilledCustomOptions();
    }
    delete this.vehicleDetailsForm.extInfoVO;
    (this.$refs.vehicleDetailsForm as any).validate((valid: any) => {
      if (valid) {
        this.saveCar(this.vehicleDetailsForm);
        // this.$emit("sendFormData", this.vehicleDetailsForm);
        // this.isDisable = true;
      }
    });
  }

  // 
  saveType() {
    // let typeDetailIds = (this.$refs.typeTree as ElTree).getCheckedKeys();
    // this.$set(this.vehicleDetailsForm, "typeDetailId", typeDetailIds.length ? typeDetailIds[0] : "");
    (this.$refs.vehicleDetailsForm as ElForm).validateField("typeDetailId", () => {});
    // this.typeVisible = false;
    if (this.vehicleDetailsForm.typeDetailId) {
      this.getCustomOptions();
    }
  }
  saveOrg() {}
}
