





































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import api from "../../../api";
import Bus from "./Bus";
import BindTable from "@/microApps/businessObjectManage/views/instance/components/BindTable.vue";
import FormCar from "./FormCar.vue";
import FormPerson from "./FormPerson.vue";
import FormBuilding from "./FormBuilding.vue";
import FormDevice from "./FormDevice.vue";
import FormObject from "./FormObject.vue";
import BindObjectTable from "@/microApps/businessObjectManage/views/ledger/components/BindObjectTable.vue";

@Component({
  components: {
    BindObjectTable,
    BindTable,
    FormCar,
    FormPerson,
    FormBuilding,
    FormDevice,
    FormObject
  }
})
export default class InstanceDetails extends Vue {
  // @Prop()
  // baseTypeId!: string;
  @Prop()
  nodeInfo!: any;
  @Prop()
  parentNodeInfo!: any;

  whichComponent: any = "FormObject";

  // 
  baseTypeInfo: any = {};
  baseTypeId: string = "";
  // 
  treeNodeInfo: any = {};
  // 
  orgInfo: any = {
    options: [],
    defaultProps: {
      label: "name",
      value: "id",
      checkStrictly: true,
      emitPath: false
    }
  };
  // tab
  baseTypeList: Array<any> = [];
  // tab
  defaultTab: string = "baseInfo";
  // 
  isDisabled: boolean = true;
  dialogVisible: boolean = false; // 

  baseTypeCode: string = "";

  objectForm: any = {}; // id
  // 
  bindShow: boolean = true;
  // bus
  busSwitch: any = null;

  mounted() {
    this.busSwitch = Bus.$on("switch", (data: any, type: string, parentNodeInfo: any) => {
      if (type === "formView") {
        this.$set(this, "objectForm", data);
        this.isDisabled = true;
        this.defaultTab = "baseInfo";
        this.formDisplay(data.businessType);
        this.bindShow = true;
        this.$nextTick(() => {
          Bus.$emit("view", data);
        });
      } else if (type === "formAdd") {
        this.baseTypeId = data.id;
        this.formDisplay(data.code);
        this.bindShow = false;
        this.$nextTick(() => {
          Bus.$emit("add", data.id, parentNodeInfo);
        });
      }
      // this.$http.get(api.base.getBaseTypeInfo + "/" + data.objectBaseTypeId).then((response: any) => {
      //   if (response.data.status) {
      //     console.log("instanceDetail");
      //     console.log(response);
      //     // this.baseTypeInfo = response.data.data;
      //     // this.formDisplay(response.data.data.code);
      //   }
      // });
    });
  }

  beforeDestroy() {
    Bus.$off([this.busSwitch]);
  }
  // ID
  // @Watch("baseTypeId", { immediate: true, deep: true })
  // getBaseTypeInfo(id: string) {
  //   console.log("");
  //   this.baseTypeInfo = {};
  //   id &&
  //     this.$http.get(api.base.getBaseTypeInfo + "/" + id).then((response: any) => {
  //       if (response.data.status) {
  //         this.baseTypeInfo = response.data.data;
  //         console.log(response.data.data);
  //         this.formDisplay(response.data.data.code);
  //       }
  //     });
  // }
  // 
  // @Watch("nodeInfo", { immediate: true, deep: true })
  getBind(data: any) {
    this.$set(this, "objectForm", data);
    this.isDisabled = true;
    this.defaultTab = "baseInfo";
    this.baseTypeInfo = {};
    data.objectBaseTypeId &&
      this.$http.get(api.base.getBaseTypeInfo + "/" + data.objectBaseTypeId).then((response: any) => {
        if (response.data.status) {
          this.baseTypeInfo = response.data.data;
          this.formDisplay(response.data.data.code);
        }
      });
  }
  tabClick(tab: any) {}
  async saveInstance() {
    return await (this.$refs["formComponent"] as any).sendFormData();
  }

  // 
  formDisplay(code: string) {
    this.baseTypeCode = code.toLowerCase();
    switch (code.toLowerCase()) {
      case "car":
        this.whichComponent = "FormCar";
        break;
      case "person":
        this.whichComponent = "FormPerson";
        break;
      case "building":
        this.whichComponent = "FormBuilding";
        break;
      case "device":
        this.whichComponent = "FormDevice";
        break;
      default:
        this.whichComponent = "FormObject";
        break;
    }
  }

  // 
  async addCar(formData: any) {
    let url: any;
    if (formData && formData.id) {
      url = api.baseInfo.updateCar;
    } else {
      url = api.baseInfo.addCar;
    }
    let {
      data: { data, status, message }
    } = await this.$http({
      method: "POST",
      url: url,
      data: formData,
      params: {
        parentId: this.parentNodeInfo.id,
        treeFlag: true
      }
    });
    if (status) {
      this.$message.success(message);
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }
  // 
  async addPerson(formData: any) {
    let {
      data: { data, status, message }
    } = await this.$http({
      method: "POST",
      url: api.baseInfo.addPerson,
      data: formData,
      params: {
        parentId: null,
        treeFlag: true
      }
    });
    if (status) {
      this.$message.success(message);
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }
  // 
  async addBuilding(formData: any) {
    let url: any;
    if (formData && formData.id) {
      url = api.baseInfo.updateBuilding;
    } else {
      url = api.baseInfo.addBuilding;
    }
    let {
      data: { data, status, message }
    } = await this.$http({
      method: "POST",
      url: url,
      data: formData,
      params: {
        parentId: null,
        treeFlag: true
      }
    });
    if (status) {
      this.$message.success(message);
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }
  // 
  async addDevice(formData: any) {
    let url: any;
    if (formData && formData.id) {
      url = api.baseInfo.updateDevice;
    } else {
      url = api.baseInfo.addDevice;
    }
    let {
      data: { data, status, message }
    } = await this.$http({
      method: "POST",
      url: url,
      data: formData,
      params: {
        parentId: null,
        treeFlag: true
      }
    });
    if (status) {
      this.$message.success(message);
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }
  // 
  async addObject(formData: any) {
    let tempVO = Object.assign({}, formData, { parentId: this.parentNodeInfo.id }, { baseTypeId: this.baseTypeId });
    let url: any;
    if (formData && formData.id) {
      url = api.ledger.updateObject;
    } else {
      url = api.ledger.addObject;
    }
    let {
      data: { data, status, message }
    } = await this.$http.post(url, tempVO);
    if (status) {
      this.$message.success(message);
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }

  getFormData(data: any) {
    switch (this.baseTypeCode) {
      case "car":
        this.addCar(data).then(() => {
          this.$emit("getGeneralNodeTree");
        });
        break;
      case "person":
        break;
      case "building":
        this.addBuilding(data).then(() => {
          this.$emit("getGeneralNodeTree");
        });
        break;
      case "device":
        this.addDevice(data).then(() => {
          this.$emit("getGeneralNodeTree");
        });
        break;
      default:
        this.addObject(data).then(() => {
          this.$emit("getGeneralNodeTree");
        });
        break;
    }
  }
}
