







































































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import { validateImage } from "@/utils/validate";
import api from "../../../api";
import Bus from "./Bus";
import Utils from "@/utils/utils";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class FormPerson extends Vue {
  @Prop()
  nodeInfo!: any;

  // 
  editableStatus: boolean = false;
  // ID
  parentId: any = null;
  // ()
  personDetailsForm: any = {};
  // ()
  personDetailsFormRules: any = {
    mobile: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ],
    typeDetailId: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ],
    realName: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ],
    roleIds: [
      {
        required: true,
        message: "",
        trigger: "blur"
      }
    ],
    organizationIds: [
      {
        required: true,
        message: "",
        trigger: "blur"
      }
    ]
  };
  // 
  personTypeVisible: boolean = false;
  personTypeOptions: Array<any> = [];
  // 
  orgOptions: Array<any> = [];
  orgVisible: boolean = false;
  // 
  roleOptions: Array<any> = [];
  // 
  disabled: boolean = false;
  // 
  visible: boolean = true;
  // 
  customOptions: Array<any> = [];
  // 
  isDisable: boolean = true;
  // 
  isEdit: boolean = true;

  created() {
    this.getRoleList();
    this.getPersonTypes();
    this.getOrgInfo();
  }
  mounted() {
    Bus.$on("add", (baseTypeId: string, parentNodeInfo: any) => {
      this.isEdit = false;
      this.isDisable = false;
      this.editableStatus = true;
      this.parentId = parentNodeInfo.id;
      this.personDetailsForm = {};
    });
    Bus.$on("view", (data: any) => {
      this.isDisable = true;
      this.editableStatus = false;
      this.getFormData(data);
    });
  }
  beforeDestroy(): void {
    // Bus.$off([this.busAdd, this.busEdit, this.busView, this.busBack]);
    Bus.$off(["add", "edit", "view", "back"]);
  }
  // @Watch("nodeInfo", { immediate: true, deep: true })
  getFormData(nodeInfo: any) {
    if (nodeInfo.businessObjectId && nodeInfo.businessType === "Person") {
      this.getPersonInfo(nodeInfo.businessObjectId).then((response: any) => {
        this.personDetailsForm = response;
      });
    } else {
      this.personDetailsForm = {};
    }
  }
  // 
  async getPersonInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.baseInfo.getPersonInfo, {
      params: {
        personId: id
      }
    });
    if (status) {
      return data;
    }
  }
  // 
  getPersonTypes() {
    this.$http.get(api.interfaceCommon.findTypeTree + "/person").then((response: any) => {
      if (response.data.status) {
        this.personTypeOptions = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 
  getRoleList() {
    this.$http.get(api.interfacePerson.roleList).then((res: any) => {
      if (res.data.status) {
        this.roleOptions = res.data.data;
        for (let i in this.roleOptions) {
          if (this.roleOptions[i].defaultRole) {
            this.personDetailsForm.roleIds = [this.roleOptions[i].id];
            break;
          }
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  // 
  getOrgInfo() {
    this.$http.get(api.interfacePerson.getNewOrgTree).then((res: any) => {
      if (res.data.status) {
        this.orgOptions = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 
  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[]) {
    let { status, message } = validateImage(file.raw);
    if (status) {
      this.$set(this.personDetailsForm, "photo", file.url);
      this.personDetailsForm.needUpload = true;
    } else {
      this.$message.error(message);
      (this.$refs.upload as any).handleRemove(file);
    }
  }
  handleRemove() {
    this.personDetailsForm.photo = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }
  // 
  customHttpRequest(data: any) {
    let fData = Utils.createUploadForm(data.file);
    this.$http
      .post(data.action, fData.formData, fData.config)
      .then((response: any) => {
        if (response.data.status) {
          // this.personDetailsForm.photo = response.data.data.accessUrl;
          this.$set(this.personDetailsForm, "image", response.data.data.accessUrl);
          // this.doSave();
        } else {
          this.$message.error(response.data.message);
        }
      })
      .finally(() => {
        // this.loading = false;
      });
  }
  // CustomInput
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["custom-input"];
    if (customInput) {
      return await customInput.formValidation();
    }
    return null;
  }
  savePersonType() {
    (this.$refs.personDetailsForm as ElForm).validateField("typeDetailId", () => {});
    if (this.personDetailsForm.typeDetailId) {
      this.getCustomOptions();
    }
  }
  // 
  getCustomOptions() {
    this.$http.get(api.interfaceCommon.getTemplateByTypeDetaiId + "/" + this.personDetailsForm.typeDetailId).then((response: any) => {
      if (response.data.status) {
        this.customOptions = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 
  saveOrg() {
    (this.$refs.personDetailsForm as ElForm).validateField("organizationIds", () => {});
  }
  doSubmit() {
    if (this.personDetailsForm.needUpload) {
      delete this.personDetailsForm.needUpload;
      (this.$refs.upload as ElUpload).submit();
    } else {
      // this.doSave();
    }
  }

  // 
  formEdit() {
    if (this.personDetailsForm.id) {
      this.editableStatus = true;
      this.isDisable = false;
      this.isEdit = true;
    } else {
      this.$message.warning("");
    }
  }
  // 
  backToInitial() {
    this.editableStatus = false;
    this.isDisable = true;
  }

  // 
  async savePerson(formData: any) {
    let url: any;
    if (this.personDetailsForm && this.personDetailsForm.id) {
      url = api.baseInfo.updatePerson;
    } else {
      url = api.baseInfo.addPerson;
    }
    let {
      data: { data, status, message }
    } = await this.$http({
      method: "POST",
      url: url,
      data: formData,
      params: {
        parentId: this.parentId,
        treeFlag: true
      }
    });
    if (status) {
      this.$message.success(message);
      this.editableStatus = false;
      this.isDisable = true;
      Bus.$emit("refresh");
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }

  async sendFormData() {
    let flag = false;
    let data = await this.getFilledCustomOptions();
    if (data) {
      this.personDetailsForm.extInfo = await this.getFilledCustomOptions();
    }
    delete this.personDetailsForm.extInfoVO;
    (this.$refs.personDetailsForm as any).validate((valid: any) => {
      if (valid) {
        this.savePerson(this.personDetailsForm);
        flag = true;
      }
    });
    return flag;
  }
}
