
































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "../../../api";
import utils from "@/utils/utils";

@Component({})
export default class MoveDialog extends Vue {
  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;
  @Prop()
  typeDetailId!: boolean;
  @Prop()
  parentNodeInfo!: any;

  // /
  selectDialogVisible: boolean = false;
  // 
  isNew: boolean = false;
  optionItem: any = {
    attrName: "",
    code: "",
    attrType: "",
    id: "",
    mustInput: 0,
    dataRange: {
      options: [],
      multiSelectedItems: []
    }
  };
  typeOptions: Array<any> = [
    {
      value: "select",
      label: ""
    },
    {
      value: "multiSelect",
      label: ""
    },
    {
      value: "number",
      label: ""
    },
    {
      value: "image",
      label: ""
    },
    {
      value: "text",
      label: ""
    },
    {
      value: "textarea",
      label: ""
    },
    {
      value: "location",
      label: ""
    },
    // {
    //   value: "eventType",
    //   label: ""
    // },
    {
      value: "link",
      label: ""
    }
  ];
  // 
  pagination: any = {
    query: {
      location: [],
      name: "",
      orgId: "",
      typeDetailId: "",
      typeId: ""
    },
    pageIndex: 1,
    total: 0,
    tableData: [],
    pageSize: 5
  };
  // 
  commonProps: any = {
    checkStrictly: true,
    emitPath: false,
    label: "name",
    value: "id"
  };
  // 
  organizations: any = {};
  // 
  detailTypeTree: any = {};
  typeObject: { [key: string]: any } = {}; // 

  @Watch("dialogVisible", { immediate: true, deep: true })
  visibleChange(newValue: boolean) {
    this.selectDialogVisible = newValue;
    if (newValue) {
      this.getOrganizations();
      this.getTypeTree(this.parentNodeInfo.objectBaseTypeId, this.parentNodeInfo.businessType.toLowerCase());
    }
  }
  @Watch("typeDetailId", { immediate: true, deep: true })
  getCanMoveList() {
    let tempVO = Object.assign({}, this.pagination, { typeDetailId: this.typeDetailId });
    this.typeDetailId &&
      this.$http.post(api.instance.getCanMoveList, tempVO).then((response: any) => {
        console.log(response);
        if (response.data.status) {
          this.pagination.tableData = response.data.data.records;
          this.pagination.total = response.data.data.total;
        }
      });
  }
  // 
  async getTypeTree(id: string, baseType: string) {
    if (this.typeObject[baseType]) {
      this.detailTypeTree = this.typeObject[baseType];
    } else {
      let {
        data: { data, status }
      } = await this.$http.get(api.ledger.getObjectTypeTree + "/" + id);
      if (status) {
        this.$set(this.typeObject, baseType, data);
        this.detailTypeTree = data;
      } else {
        // this.$message.error(message)
      }
    }
  }
  // 
  async getOrganizations() {
    let {
      data: { data, status }
    } = await this.$http.get(api.ledger.getOrganizationTree);
    if (status) {
      // this.organizationTree = data;
      utils.removeEmptyChildren(data);
      // this.$set(this, "organizationTree", data);
      this.organizations = data;
    } else {
      // this.$message.error(message)
    }
  }
  // 
  changeParentNode(row: any) {
    console.log(row);
    this.$http
      .get(api.instance.changeParentNode, {
        params: {
          id: this.parentNodeInfo.id,
          newParentId: row.id
        }
      })
      .then((response: any) => {
        if (response.data.status) {
          this.$message.success(response.data.message);
          this.closeDialog();
          this.$emit("getGeneralNodeTree");
        } else {
          this.$message.error(response.data.message);
        }
      });
  }
  // 
  closeDialog() {
    // this.selectDialogVisible = false;
    this.$emit("closeMoveDialog");
  }
  // Panel
  openPanel() {
    this.$nextTick(() => {
      this.isNew = !this.isNew;
      // console.log(this.$refs["optionItemForm"]);
      // this.$refs["optionItemForm"] && (this.$refs["optionItemForm"] as any).resetFields();
      this.optionItem = {
        attrName: "",
        code: "",
        attrType: "",
        // id: "",
        mustInput: 0,
        dataRange: {
          options: [],
          multiSelectedItems: []
        }
      };
    });
  }
  // 
  search() {
    this.pagination.current = 1;
    this.pagination.total = 0;
    this.getCanMoveList();
  }
  handleSizeChange() {}

  handleCurrentChange() {}
}
