












































































































import { Component, Vue, Watch } from "vue-property-decorator";
import InstanceDetails from "@/microApps/businessObjectManage/views/instance/components/InstanceDetails.vue";
import api from "../../api";
import SelectDialog from "@/microApps/businessObjectManage/views/instance/components/SelectDialog.vue";
import MoveDialog from "@/microApps/businessObjectManage/views/instance/components/MoveDialog.vue";
import Bus from "./components/Bus";

@Component({
  components: {
    MoveDialog,
    SelectDialog,
    InstanceDetails
  }
})
export default class Instance extends Vue {
  // 
  treeMessage: any = {
    // 
    filterText: "",
    // tree
    data: [],
    // 
    defaultProps: {
      children: "children",
      label: "name"
    }
  };
  // 
  basicForm: any = {};
  // 
  topCascaderInfo: any = {
    data: [
      {
        id: "xinjianshili",
        baseTypeName: "",
        children: []
      },
      {
        id: "xuanzeshili",
        baseTypeName: "",
        children: []
      }
    ],
    defaultProps: {
      children: "children",
      label: "baseTypeName",
      value: "id",
      emitPath: true,
      lazy: true,
      lazyLoad: (node: any, resolve: any) => {
        this.getObjectBaseType().then((response: any) => {
          let nodes = response.map((item: any) => {
            return {
              id: item,
              baseTypeName: item.baseTypeName,
              leaf: true
            };
          });
          resolve(nodes);
        });
      }
    }
  };
  // 
  cascaderInfo: any = {
    data: [
      {
        id: "xinjianxiashushili",
        baseTypeName: "",
        children: []
      },
      {
        id: "xuanzexiashushili",
        baseTypeName: "",
        children: []
      },
      // {
      //   id: "yidongzhi",
      //   baseTypeName: ""
      // },
      {
        id: "shanchushili",
        baseTypeName: ""
      }
    ],
    defaultProps: {
      children: "children",
      label: "baseTypeName",
      value: "id",
      emitPath: true
    }
  };
  // ID
  baseTypeId: string = "";
  // 
  nodeInfo: any = {};
  // 
  parentNodeInfo: any = {};
  // ID
  typeDetailId: string = "";
  // 
  toolShow: boolean = false;
  // 
  editableStatus: boolean = false;
  // 
  topMenuVisible: boolean = false;
  // 
  menuVisible: boolean = false;
  // 
  selectDialogVisible: boolean = false;
  // 
  moveDialogVisible: boolean = false;
  // bus
  busRefresh: any = null;

  created() {
    this.getGeneralNodeTree();
  }

  mounted() {
    document.addEventListener("click", this.menuHide, false);
    this.busRefresh = Bus.$on("refresh", () => {
      this.getGeneralNodeTree();
    });
  }

  beforeDestroy() {
    document.removeEventListener("click", this.menuHide, false);
    Bus.$off(this.busRefresh);
  }

  // tree
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }
  // tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }
  // 
  getGeneralNodeTree() {
    this.$http.get(api.instance.getGeneralNodeTree).then((response: any) => {
      if (response.data.status) {
        this.treeMessage.data = response.data.data;
      }
    });
  }
  // 
  async handleNodeClick(data: any) {
    this.menuVisible = false;
    this.topMenuVisible = false;
    this.toolShow = true;
    this.editableStatus = false;
    this.nodeInfo = data;
    // Bus.$emit("view", data);
    Bus.$emit("switch", data, "formView");
  }
  // 
  openTopCascader(event: any) {
    this.topMenuVisible = !this.topMenuVisible;
    (this.$refs.topCascader as any).clearCheckedNodes();
    let menu: any = this.$refs["topMenu"];
    /*  */
    menu.style.left = (window.event as any).clientX - 250 + "px";
    menu.style.top = (window.event as any).clientY - 130 + "px";
  }
  // 
  topMenuChange(node: any) {
    this.baseTypeId = "";
    if (node.length) {
      if (node[0] === "xinjianshili") {
        Bus.$emit("switch", node[1], "formAdd", {});

        this.topMenuVisible = false;
        this.baseTypeId = node[1].id;
        // this.parentId = null;
        this.toolShow = true;
        this.editableStatus = true;

        this.parentNodeInfo = {};
      } else if (node[0] === "xuanzeshili") {
        this.topMenuVisible = false;
        this.selectDialogVisible = true;

        this.baseTypeId = node[1].id;
        this.nodeInfo = {};
        this.parentNodeInfo = {};
      }
    }
  }
  // 
  async getObjectBaseType() {
    let {
      data: { status, data }
    } = await this.$http.get(api.base.getObjectBaseType);
    if (status) {
      return data;
    }
  }
  // 
  async nodeContextmenu(event: any, data: any, node: any) {
    this.menuVisible = true;
    (this.$refs.nodeCascader as any).clearCheckedNodes();
    let menu: any = this.$refs["menu"];
    /*  */
    menu.style.position = "absolute";
    menu.style.left = event.clientX - 250 + "px";
    menu.style.top = event.clientY - 130 + "px";
    this.$nextTick(() => {
      if (this.$refs.tree) {
        (this.$refs.tree as any).setCurrentKey(data.id);
      }
    });

    this.parentNodeInfo = data;
    Bus.$emit("switch", data, "formView");

    let objectDetail: any;
    switch (data.businessType) {
      case "Car":
        objectDetail = await this.getCarInfo(data.businessObjectId);
        break;
      case "Person":
        objectDetail = await this.getPersonInfo(data.businessObjectId);
        break;
      case "Building":
        objectDetail = await this.getBuildingInfo(data.businessObjectId);
        break;
      case "Device":
        objectDetail = await this.getDeviceInfo(data.businessObjectId);
        break;
      case "GeneralObject":
        objectDetail = await this.getObjectDetail(data.businessObjectId);
        break;
    }
    let baseTypeList: any = await this.getBaseTypeList(objectDetail.typeDetailId);
    this.cascaderInfo.data[0].children = baseTypeList.map((item: any) => {
      return {
        id: item,
        baseTypeName: item.baseTypeName,
        leaf: true
      };
    });
    this.cascaderInfo.data[1].children = baseTypeList.map((item: any) => {
      return {
        id: item,
        baseTypeName: item.baseTypeName,
        leaf: true
      };
    });
    this.typeDetailId = objectDetail.typeDetailId;
  }
  // 
  async getObjectDetail(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.ledger.getObjectDetail, {
      params: {
        id: id
      }
    });
    if (status) {
      return data;
    }
  }
  // 
  async getBaseTypeList(typeDetailId: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.instance.getBaseTypeList + "/" + typeDetailId);
    if (status) {
      return data;
    }
  }
  // 
  menuChange(node: any) {
    this.baseTypeId = "";
    if (node.length) {
      this.menuVisible = false;
      if (node[0] === "xinjianxiashushili") {
        // this.parentId = node[1].father.id;

        Bus.$emit("switch", node[1], "formAdd", this.parentNodeInfo);

        this.baseTypeId = node[1].id;
        this.nodeInfo = {};
        this.toolShow = true;
        this.editableStatus = true;
      } else if (node[0] === "xuanzexiashushili") {
        this.selectDialogVisible = true;
        this.baseTypeId = node[1].id;
      } else if (node[0] === "yidongzhi") {
        this.moveDialogVisible = true;
      } else if (node[0] === "shanchushili") {
        this.deleteNode();
      }
    }
  }
  // 
  closeSelectDialog() {
    this.getGeneralNodeTree();
    this.selectDialogVisible = false;
  }
  // 
  closeMoveDialog() {
    this.moveDialogVisible = false;
  }
  // 
  editInstance() {
    this.editableStatus = true;
    Bus.$emit("edit");
  }
  // 
  backToInitial() {
    this.editableStatus = false;
    Bus.$emit("back");
  }
  // 
  saveInstance() {
    (this.$refs["instanceDetails"] as any).saveInstance().then((response: any) => {
      if (response) {
        this.toolShow = false;
        this.editableStatus = false;
        Bus.$emit("back");
      }
    });
  }
  // 
  deleteNode() {
    this.$confirm("?", "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: "warning"
    })
      .then(() => {
        this.$http
          .get(api.instance.deleteNode, {
            params: {
              id: this.parentNodeInfo.id,
              flag: true
            }
          })
          .then((response: any) => {
            this.getGeneralNodeTree();
            if (response.data.status) {
              this.$message.success(response.data.message);
            } else {
              this.$message.error(response.data.message);
            }
          });
      })
      .catch(() => {});
  }
  menuHide(e: any) {
    // console.log("");
    // console.log("!!!!!!!!!!!!!!e.targetName=", e.target.nodeName);
    if (!(this.$refs.menu as any).contains(e.target)) {
      // console.log(e.target);
      // console.log("right!!!!!!!!!");
      this.menuVisible = false;
    }
  }

  // 
  async getCarInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.baseInfo.getCaInfo + "/" + id);
    if (status) {
      return data;
    }
  }
  // 
  async getPersonInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.baseInfo.getPersonInfo, {
      params: {
        personId: id
      }
    });
    if (status) {
      return data;
    }
  }
  // 
  async getBuildingInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.baseInfo.getBuildingInfo + "/" + id);
    if (status) {
      return data;
    }
  }
  // 
  async getDeviceInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.baseInfo.getDeviceInfo + "/" + id);
    if (status) {
      return data;
    }
  }
}
