






























































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import api from "../../../api";
import Bus from "./Bus";
import { ElForm } from "element-ui/types/form";
import Utils from "@/utils/utils";
import { validateImage } from "@/utils/validate";

const AMap: any = (window as any).AMap;
const AMapUI: any = (window as any).AMapUI;

@Component({})
export default class FormBuilding extends Vue {
  @Prop()
  nodeInfo!: any;

  // 
  editableStatus: boolean = false;
  // ID
  parentId: any = null;
  loading: boolean = false;
  // 
  customOptions: Array<any> = [];
  // ()
  detailsForm: any = {};
  // ()
  detailsFormRules: any = {
    name: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ],
    // code: [
    //   {
    //     required: true,
    //     message: "",
    //     trigger: "change"
    //   }
    // ],
    orgId: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ],
    typeDetailId: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ]
  };
  // 
  orgOptions: Array<any> = [];
  typeDetailOptions: Array<any> = [];
  // 
  disabled: boolean = false;
  //
  visible: boolean = true;
  hasCustomRole: boolean = qTouch.getBaseTypeByCode("building");
  // 
  map: any;
  marker: any = null;
  addressDialog = false;
  text = "";
  showLeaveChoose = false;
  zoom = 18;
  center = [106.569137124949, 29.643814122592];
  // 
  isDisable: boolean = true;
  // 
  isEdit: boolean = true;

  created() {
    this.getOrgTree();
    this.getTypeDetailTree();
  }
  mounted() {
    Bus.$on("add", (baseTypeId: string, parentNodeInfo: any) => {
      this.isEdit = false;
      this.isDisable = false;
      this.editableStatus = true;
      this.parentId = parentNodeInfo.id;
      this.detailsForm = {};
    });
    Bus.$on("view", (data: any) => {
      this.isDisable = true;
      this.editableStatus = false;
      this.getFormData(data);
    });
  }
  beforeDestroy(): void {
    // Bus.$off([this.busAdd, this.busEdit, this.busView, this.busBack]);
    Bus.$off(["add", "edit", "view", "back"]);
  }
  // @Watch("nodeInfo", { immediate: true, deep: true })
  getFormData(nodeInfo: any) {
    if (nodeInfo.businessObjectId && nodeInfo.businessType === "Building") {
      this.isDisable = true;
      this.getBuildingInfo(nodeInfo.businessObjectId).then((response: any) => {
        this.detailsForm = response;
      });
    } else {
      this.isDisable = false;
      this.detailsForm = {};
    }
  }
  // 
  async getBuildingInfo(id: string) {
    let {
      data: { data, status }
    } = await this.$http.get(api.baseInfo.getBuildingInfo + "/" + id);
    if (status) {
      return data;
    }
  }
  // CustomInput
  async getFilledCustomOptions() {
    let data: any;
    let customInput: any = this.$refs["custom-input"];
    if (customInput) {
      return await customInput.formValidation();
    }
    // if (data) {
    //   this.detailsForm.extInfo = data;
    // }
    // this.doSave(); //
  }
  // 
  saveInfo() {
    //
    (this.$refs.detailsForm as any).validate((valid: any) => {
      if (valid) {
        if (this.detailsForm.needUpload) {
          this.loading = true;
          delete this.detailsForm.needUpload;
          (this.$refs.upload as ElUpload).submit();
        } else {
          //
          this.getFilledCustomOptions();
        }
      }
    });
  }
  //
  getTypeDetailTree() {
    this.queryTree(api.interfaceCommon.findTypeTree + "/building", "building");
  }
  // 
  saveType() {
    (this.$refs.detailsForm as ElForm).validateField("typeDetailId", () => {});
    if (this.detailsForm.typeDetailId) {
      this.changeCustomOptions();
    }
  }
  //
  getOrgTree() {
    this.queryTree(api.interfaceBuilding.getNewOrgTree, "organization");
  }
  ///
  queryTree(url: any, obj: any) {
    this.$http.get(url).then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          Utils.removeEmptyChildren(res.data.data);
          if (obj == "building") {
            this.typeDetailOptions = res.data.data;
          }
          if (obj == "organization") {
            this.orgOptions = res.data.data;
          }
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }

  //
  changeCustomOptions() {
    this.$http.get(api.interfaceCommon.getTemplateByTypeDetaiId + "/" + this.detailsForm.typeDetailId).then((res: any) => {
      if (res.data.status) {
        this.customOptions = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 
  customHttpRequest(data: any) {
    this.loading = true;
    let fData = Utils.createUploadForm(data.file);
    this.$http
      .post(data.action, fData.formData, fData.config)
      .then((response: any) => {
        this.loading = false;
        if (response.data.status) {
          // this.detailsForm.image = response.data.data.accessUrl;
          this.$set(this.detailsForm, "image", response.data.data.accessUrl);
          // this.doSave(); //
        } else {
          this.$message.error("");
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[]) {
    let { status, message } = validateImage(file.raw);
    if (status) {
      this.$set(this.detailsForm, "image", file.url);
      this.detailsForm.needUpload = true;
    } else {
      this.$message.error(message);
      (this.$refs.upload as any).handleRemove(file);
    }
  }

  handleRemove() {
    this.detailsForm.image = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }

  // 
  bindAddress() {
    this.addressDialog = true;
    this.$nextTick(() => {
      // 
      if (this.detailsForm.location) {
        let lnglat = JSON.parse(this.detailsForm.location);
        this.initMap(lnglat); //  
        this.addBuildMarker(lnglat, this.detailsForm.address); // 
        this.showLeaveChoose = false;
        this.text = "" + this.detailsForm.location;
      }
      // 
      else {
        this.text = "";
        // 
        this.initMap(this.center);
        this.chooseSiteOnMap();
      }
    });
  }

  // 
  initMap(center: any) {
    const _this = this;
    this.map = Object.freeze(
      new AMap.Map("buildAmap", {
        center: center,
        resizeEnable: true,
        zoom: this.zoom,
        features: ["bg", "point", "road", "building"]
      })
    );
    this.map.setMapStyle("amap://styles/7a2258c5592a544ff4242a2d0341ef6c");
    AMapUI.loadUI(["misc/PoiPicker"], function(PoiPicker: any) {
      var poiPicker = new PoiPicker({
        input: "buildInput"
      });
      //poiPicker
      _this.poiPickerReady(poiPicker);
    });
  }

  poiPickerReady(poiPicker: any) {
    const _this = this;
    (window as any).poiPicker = poiPicker;
    let marker = new AMap.Marker();
    // let infoWindow = new AMap.InfoWindow({
    //   offset: new AMap.Pixel(0, -20)
    // });
    // POI
    poiPicker.on("poiPicked", function(poiResult: any) {
      let source = poiResult.source;
      let poi = poiResult.item;
      let info = {
        source: source,
        id: poi.id,
        name: poi.name,
        location: poi.location.toString(),
        address: poi.formattedAddress
      };
      marker.setMap(_this.map);
      // infoWindow.setMap(_this.map);
      marker.setPosition(poi.location);
      // infoWindow.setPosition(poi.location);
      // // infoWindow.setContent("POI: <pre>" + JSON.stringify(info, null, 2) + "</pre>");
      // infoWindow.open(_this.map, marker.getPosition());
      _this.map.setCenter(marker.getPosition());
    });
  }

  // marker
  addBuildMarker(position: any, address: string) {
    let _this = this;
    this.marker = new AMap.Marker({
      position: position
    });
    this.marker.setLabel({
      //labelmaker
      offset: new AMap.Pixel(20, -20),
      content: "<div style='margin: 2px 5px; font-family: PingFang SC;line-height: 16px'>" + address + "</div>"
    });
    this.marker.setMap(this.map);
  }

  // 
  chooseOff() {
    this.text = "";
    this.showLeaveChoose = false;
    this.initMap(this.center);
  }

  // 
  chooseSiteOnMap() {
    let _this = this;
    this.showLeaveChoose = true;
    this.text = "";
    this.map.clearMap();
    // (  )
    this.map.setDefaultCursor("crosshair");
    this.map.on("dblclick", function(e: any) {
      let lnglat = [e.lnglat.getLng(), e.lnglat.getLat()];
      _this.text = " " + JSON.stringify(lnglat) + " ";
      let geocoder = new AMap.Geocoder({
        radius: 1000,
        extensions: "all"
      });
      geocoder.getAddress(lnglat, function(status: any, result: any) {
        if (status === "complete" && result.info === "OK") {
          let address = result.regeocode.formattedAddress; //
          _this
            .$prompt("", "", {
              confirmButtonText: "",
              cancelButtonText: "",
              inputValue: address
            })
            .then((obj: any) => {
              _this.detailsForm.location = JSON.stringify(lnglat);
              _this.detailsForm.address = obj.value;
              _this.addressDialog = false;
            })
            .catch(() => {
              // _this.$message.info("");
              _this.text = "";
            });
        }
      });
    });
  }
  destroyMap() {
    this.map.destroy();
  }

  // 
  formEdit() {
    if (this.detailsForm.id) {
      this.editableStatus = true;
      this.isDisable = false;
      this.isEdit = true;
    } else {
      this.$message.warning("");
    }
  }
  // 
  backToInitial() {
    this.editableStatus = false;
    this.isDisable = true;
  }

  // 
  async saveBuilding() {
    let url: any;
    if (this.detailsForm && this.detailsForm.id) {
      url = api.baseInfo.updateBuilding;
    } else {
      url = api.baseInfo.addBuilding;
    }
    let {
      data: { data, status, message }
    } = await this.$http({
      method: "POST",
      url: url,
      data: this.detailsForm,
      params: {
        parentId: this.parentId,
        treeFlag: true
      }
    });
    if (status) {
      this.$message.success(message);
      this.isDisable = true;
      this.editableStatus = false;
      Bus.$emit("refresh");
      return data;
    } else {
      this.$message.error(message);
      return null;
    }
  }

  async sendFormData() {
    let flag = false;
    let data = await this.getFilledCustomOptions();
    if (data) {
      this.detailsForm.extInfo = await this.getFilledCustomOptions();
    }
    delete this.detailsForm.extInfoVO;
    (this.$refs.detailsForm as any).validate((valid: any) => {
      if (valid) {
        this.saveBuilding();
        // this.$emit("sendFormData", this.detailsForm);
        flag = true;
      }
    });
    return flag;
  }
}
