


















































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "../../../api";

@Component({})
export default class SelectDialog extends Vue {
  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;
  @Prop()
  baseTypeId!: string;
  @Prop()
  parentNodeInfo!: any;

  // 
  baseTypeInfo: any = {};

  // /
  selectDialogVisible: boolean = false;
  // 
  isNew: boolean = false;
  optionItem: any = {
    attrName: "",
    code: "",
    attrType: "",
    id: "",
    mustInput: 0,
    dataRange: {
      options: [],
      multiSelectedItems: []
    }
  };
  typeOptions: Array<any> = [
    {
      value: "select",
      label: ""
    },
    {
      value: "multiSelect",
      label: ""
    },
    {
      value: "number",
      label: ""
    },
    {
      value: "image",
      label: ""
    },
    {
      value: "text",
      label: ""
    },
    {
      value: "textarea",
      label: ""
    },
    {
      value: "location",
      label: ""
    },
    // {
    //   value: "eventType",
    //   label: ""
    // },
    {
      value: "link",
      label: ""
    }
  ];
  // 
  keyword: string = "";
  transferData: any = {
    data: [],
    selected: []
  };
  defaultProps: any = {
    car: {
      key: "id",
      label: "licensePlate"
    },
    person: {
      key: "userId",
      label: "userName"
    }
  };

  @Watch("dialogVisible", { immediate: true, deep: true })
  visibleChange(newValue: boolean) {
    this.selectDialogVisible = newValue;
    if (newValue) {
      this.$nextTick(() => {
        (this.$refs["transfer"] as any).clearQuery("left");
        (this.$refs["transfer"] as any).clearQuery("right");
      });
    }
  }

  // mounted() {
  //   this.getAllObjectByType();
  // }
  // 
  @Watch("baseTypeId", { immediate: true, deep: true })
  getBaseTypeInfo(id: string) {
    this.baseTypeInfo = {};
    id &&
      this.$http.get(api.base.getBaseTypeInfo + "/" + id).then((response: any) => {
        if (response.data.status) {
          this.baseTypeInfo = response.data.data;
        }
      });
    this.transferData.data = [];
    this.transferData.selected = [];
    id &&
      this.$http
        .get(api.objectDetail.getAllUnBind, {
          params: {
            keyword: this.keyword,
            objectBaseTypeId: id,
            id: this.parentNodeInfo.id || ""
          }
        })
        .then((response: any) => {
          if (response.data.status) {
            this.transferData.data = response.data.data;
          }
        });
  }
  // 
  updateBind() {
    console.log(this.parentNodeInfo.id);
    console.log(this.baseTypeId);
    console.log(this.baseTypeInfo.code);
    console.log(this.transferData.selected);
    let type = "";
    if (
      this.baseTypeInfo.code === "car" ||
      this.baseTypeInfo.code === "person" ||
      this.baseTypeInfo.code === "building" ||
      this.baseTypeInfo.code === "device"
    ) {
      type = this.baseTypeInfo.code.replace(/^\S/, (s: any) => s.toUpperCase());
    } else {
      type = "GeneralObject";
    }
    let tempVO = Object.assign(
      {},
      { baseObjectTypeId: this.baseTypeId },
      { currentId: this.parentNodeInfo.id || null },
      { ids: this.transferData.selected },
      { type: type }
    );
    this.$http.post(api.objectDetail.updateBinding, tempVO).then((response: any) => {
      if (response.data.status) {
        this.$message.success(response.data.message);
        this.$emit("closeSelectDialog");
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 
  closeDialog() {
    // this.selectDialogVisible = false;
    this.$emit("closeSelectDialog");
  }
  // Panel
  openPanel() {
    this.$nextTick(() => {
      this.isNew = !this.isNew;
      // console.log(this.$refs["optionItemForm"]);
      // this.$refs["optionItemForm"] && (this.$refs["optionItemForm"] as any).resetFields();
      this.optionItem = {
        attrName: "",
        code: "",
        attrType: "",
        // id: "",
        mustInput: 0,
        dataRange: {
          options: [],
          multiSelectedItems: []
        }
      };
    });
  }
  // 
  search() {}
}
